.site-footer {
  @apply container--full border-t border-black/10 py-10 text-sm lg:py-16;
  address {
    @apply mb-5 not-italic;
    ul {
      @apply flex max-w-xl flex-row flex-wrap gap-x-3 gap-y-0.5 lg:gap-y-1.5;
      li {
        strong {
          @apply mr-0.5 pr-0.5 font-normal opacity-70;
        }
        span {
          @apply text-black/80;
          a {
            @apply text-black/80;
          }
        }
      }
    }
  }
  .site-info {
    @apply text-xs opacity-50;
  }
  em {
    @apply mr-1.5 mt-0.5 inline-block h-3 border-r border-slate-400 pl-1.5 not-italic opacity-40;
  }
}
#scroll-top {
  @apply fixed -right-10 bottom-10 z-10 bg-black/10 leading-none transition-all duration-500 ease-in-out;
}

#page.aos-animate {
  #scroll-top {
    @apply right-0;
  }
}
